@import '../../styles/sass/variables';

.title {
  margin-top: 30px;
  margin-bottom: 30px;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 300;
  color: black;
  text-transform: capitalize;
  font-size: 18px;
  line-height: 1.2;
  letter-spacing: 5px;
  position: relative;

  h2 {
    margin: 0;
  }

  &::before {
    content: '';
    position: absolute;
    background: $btnColor;
    width: 55px;
    height: 1px;
    bottom: -30px;
    transform: translate(-50%, 50%);
    left: 50%;
  }
}

@media (max-width: 767px) {
  .title {
    margin-top: 50px;
    margin-bottom: 10px;
    font-size: 16px;
    letter-spacing: 3px;
  }
}
